body {
	display:flex;
	
margin:auto;

 
  font-family: 'Staatliches',sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #353232;
  color:white;

  justify-content: center;


}

code {
