}

#add-form{
  display: inline-block;
}

.modal {
  width: 300px;
  height:250px;
  position: absolute;
  top:10px;
  background-color: #353232;
  /*margin: 9px auto;*/
  z-index: 2;
  border: solid pink  2px;
  border-radius: 10px;
  display:flex;
